/*MEDIA*/
@media(max-width: 1200px) and (min-width: 1024px) {
    .card {
        padding: 5px;
        gap: 5px;
        width: 70vw;
    }

    .card-text {
        font-size: 12px;
    }

    .container {
        .lady {
            position: absolute;
            top: 100px;
            right: 100px;
        }
    }
}

@media(max-width: 1024px) and (min-width: 992px) {
    .menu-text {
        gap: 10px;
    }

    #logoname {
        font-size: 30px;
    }

    li {
        a {
            margin-left: 5vw;
        }
    }

    .container {
        .lady {
            position: absolute;
            top: 100px;
            right: 100px;
        }
    }

    .table-static {
        grid-template-columns: repeat(2, 1fr);
        grid-template-rows: 3fr;
        width: 60vw;
        gap: 10px;
        align-items: center;
    }

    .card {
        padding: 5px;
        gap: 5px;
        width: 70vw;
    }

    .card-text {
        font-size: 12px;
    }

    .leftpart-footer {
        margin-right: 30%;
    }
}

@media(max-width: 992px) and (min-width: 768px) {
    .menu-text {
        margin-right: 0px;
    }

    li>a {
        margin-left: 0px;
    }

    #phone {
        left: 35px;
        top: 18px;
    }

    .language-menu {
        position: relative;
        top: 50px;
        left: -40px;
    }

    .menu-main {
        .container {
            .lady {
                left: 100px;
                height: 170px;
                top: 247px
            }
        }
    }

    .gallery {
        flex-direction: column;
    }

    .foto {
        margin-bottom: 15px;
    }

    .table-static {
        grid-template-columns: repeat(2, 300px);
        grid-template-rows: 3fr;
        padding-left: 10px;
        padding-right: 0;
        gap: 10px;
        justify-content: left;
    }

    .card-all {
        flex-direction: column;
        align-items: start;
        margin-left: 10px;
    }

    .table-all {
        text-align: left;
        margin-left: 10px;
    }

    .text-title {
        width: 20vw;
        font-size: 24px;
    }

    .text {
        margin-top: 5px;
        width: 612px;
        margin: 0;
        margin-left: 5px;
    }

    .teeth {
        padding: 10px;
        align-items: flex-start;
    }

    .teeth-circle {
        margin-left: 0;
        margin-right: 0;
        display: flex;
        flex-direction: column;
        align-self: flex-start;
        padding: 10px;
    }

    .atetion {
        justify-content: left;
    }

    .user-btn {
        display: flex;
        flex-direction: column;
    }

    .user-data {
        width: 520px;
        margin-left: 10px;
    }

    .container-btn {
        height: auto;
        width: 500px;
    }

    .user-btn {
        align-items: center;
    }

    .one-btn {
        margin-top: 10px;
    }

}

@media(max-width: 768px) and (min-width: 480px) {
    .menu-text {
        flex-direction: column;
    }

    li>a {
        margin-left: 0px;
    }

    .menu {
        div {
            #phone {
                left: -40px;
                top: 50px;
                font-size: 12px;
            }
        }
    }

    .language-menu {
        display: none;
    }

    .card-all {
        flex-direction: column;
        align-items: center;
        margin-left: 10px;
    }

    .menu-main {
        .container {
            .lady {
                left: 100px;
                height: 170px;
                top: 247px
            }
        }
    }

    .table-all {
        text-align: center;
        margin-left: 5px;
    }

    .table-static {
        grid-template-columns: repeat(2, 300px);
        grid-template-rows: 3fr;
        padding-left: 10px;
        padding-right: 0;
        gap: 10px;
        justify-content: left;
    }

    .cell {
        width: 240px;
    }

    .text-title {
        width: 70vw;
        font-size: 24px;
        margin-left: 5px;
    }

    .text {
        margin-top: 5px;
        width: 612px;
        margin: 0;
        margin-left: 5px;
    }

    .teeth {
        padding: 10px;
        align-items: flex-start;
    }

    .teeth-circle {
        margin-left: 0;
        margin-right: 0;
        display: flex;
        flex-direction: column;
        align-self: flex-start;
        padding: 10px;
    }

    .for-teeth-leftpart {
        align-items: center;
    }

    .atetion {
        justify-content: center;
    }

    .user-btn {
        display: flex;
        flex-direction: column;
    }

    .user-data {
        width: 520px;
        margin: 0 auto;
    }

    .container-btn {
        height: auto;
        width: 500px;
    }

    .user-btn {
        align-items: center;
    }

    .one-btn {
        margin-top: 10px;
    }

    .gallery {
        flex-direction: column;
    }

    .foto {
        margin-bottom: 15px;
    }

    footer {
        flex-direction: column;
        align-items: flex-start;
    }

    .dark-logo>span:first-child {
        font-size: 27px;
    }

    .leftpart-footer {
        margin-right: 0px;
        padding-left: 10px;
    }

    .white-text {
        padding-left: 10px;
    }

    .text-footer {
        grid-template-columns: 150px 1fr;
    }
}


@media(max-width: 480px) {
    .menu {
        .logo-top {
            position: relative;
            top: -150px;
        }

        .header-menu__wrap {
            display: flex;
        }
    }

    .logo-text {
        span {
            font-size: 12px;
        }
    }

    .menu {
        .logo-text {
            #logoname {
                font-size: 12px;
                text-shadow: #000 0px 1px 1px;
            }
        }

        div {
            #phone {
                top: 7px;
                left: -200px;
                font-size: 10px;
            }
        }
    }

    .language-menu {
        display: none;
    }

    .menu-text {
        display: none;
    }

    .menu-main {
        width: 130%;

        .container {
            text-align: center;

            h1,
            .title-blue_text {
                font-size: 22px;
            }

            .title-text {
                font-size: 12px;
                margin-bottom: 0px;
            }

            .btn {
                display: flex;
                margin-left: 5vw;

                .btn-tel {
                    margin-left: 0;
                    margin-right: 50px;
                }
            }

            .lady {
                left: 15px;
                height: 200px;
                top: 230px
            }

            .card-all {
                flex-direction: column;
                align-content: center;
                margin-top: 180px;
            }
        }
    }

    .table-all {
        text-align: left;
        margin-left: 1vw;
        text-align: center;

        .text-title {
            text-transform: uppercase;
            font-size: 20px;
            width: 60vw;
            margin-left: 0;
        }

        .text--table {
            width: 60vw;
        }
    }

    .text {
        margin-left: 0;
        width: 300px;
    }

    .user-data {
        display: none;
    }

    .table-all {
        .table-static {
            display: flex;
            flex-direction: column;
            background-color: #A1C7D4;
            border-radius: 10px;
            padding: 16px 20px;
            margin-top: 50px;

            .cell {
                width: 70vw;
            }
        }
    }

    .teeth {
        .teeth-circle {
            margin: 0 auto;
            display: flex;
            flex-direction: column;
        }
    }

    .for-teeth-leftpart {
        text-align: center;
    }

    .center-text {
        margin-right: 0px;
    }

    .teeth-center--2>div {
        padding-left: 0;
    }

    .atetion {
        display: flex;
        flex-direction: column;
    }

    .gallery {
        flex-direction: column;
    }

    .foto {
        margin-bottom: 15px;
    }

    .teeth-top--1,
    .teeth-top--2 {
        animation: none;
    }

    .menu-main {
        width: 130%;
    }

    .title-text {
        font-size: 12px;
    }

    .center-text {
        margin-right: 0px;
    }

    .teeth-center--2>div {
        padding-left: 0;
    }

    .atetion {
        display: flex;
        flex-direction: column;
    }

    .gallery {
        flex-direction: column;
    }

    .foto {
        margin-bottom: 15px;
    }

    .teeth-top--1,
    .teeth-top--2 {
        animation: none;
    }

    .teeth-center--1,
    .teeth-center--2,
    .teeth-bottom--1,
    .teeth-bottom--2 {
        animation: none;
    }

    footer {
        flex-direction: column;
        align-items: flex-start;
    }

    .leftpart-footer {
        margin-right: 0px;
        padding-left: 10px;
    }

    .dark-logo span:first-child {
        font-size: 18px;
    }

    .white-text {
        padding-left: 10px;
    }

    .text-footer {
        grid-template-columns: 150px 1fr;
    }
}